import React, { ErrorInfo, useState } from 'react';
import './App.css';
import ReactAudioPlayer from 'react-audio-player';
import {BrowserRouter as Router, useLocation, Switch, Route} from 'react-router-dom'
import * as privacyPolicy from './privacypolicy'
import ReactMarkdown from 'react-markdown';


function App() {
  const markdown = `
  # Header 1
  ## Header 2

  _ italic _

  ** bold **

  <b> bold Html </b>
  `;
  return (
    <div className="App">
      <header className="App-header">   
      <Router>
        <Switch>
          <Route exact path="/">
            <AudioPlayer/>    
            <FetchedDescription/>    
          </Route>
          <Route path="/privacy">
            <ReactMarkdown>{privacyPolicy.text}</ReactMarkdown>
          </Route>
        </Switch>        
      </Router>      
      </header>
    </div>
  );
}


function AudioPlayer(){
  const rawLocationSearch = useLocation().search;
  const locationSearch = rawLocationSearch.slice(-(rawLocationSearch.length-1));

  const [errored, setErrored] = React.useState(false);

  if(errored){
    return <h1>Unable to load snippet</h1>
  }

  const onAudioPlayerError = (error: any)=>{
    console.log("Error: ", error);
    setErrored(true);
  }
  
  try{
    const query = new URLSearchParams((locationSearch));
    const val = query.get('val');    
      
    const endpoint = 'https://letsusespeakup.com/backend/open/snippets?val=' + val;
    // const endpoint = 'http://localhost:1234/backend/open/snippets?val=' + val;
  
    return(<ReactAudioPlayer    
      src={endpoint}
      autoPlay={true}
      controls={true}
      loop={true}
      controlsList={'nodownload'}      
      onError={onAudioPlayerError}
    />)
  }
  catch(error){    
    console.log("ERROR: ", error);
    setErrored(true);
    return <h1>Unable to load snippet</h1>
  }  
}

function FetchedDescription(){  
  const [description, setDescription] = useState('A snippet from SpeakUp')
  const rawLocationSearch = useLocation().search;
  try{
    const locationSearch = rawLocationSearch.slice(-(rawLocationSearch.length-1));
    const query = new URLSearchParams((locationSearch));
    const val = query.get('val'); 

    const endpoint = 'https://letsusespeakup.com/backend/open/snippets/snippetdescription?val=' + val;
    // const endpoint = 'http://localhost:1234/backend/open/snippets/snippetdescription?val=' + val;      
    fetch(endpoint).then((response)=> response.json())
    .then((data)=>{
      const fetchedDescription = data.description;
      if(fetchedDescription) setDescription(fetchedDescription);
    }).catch((error)=>{
      console.log("ERROR -- FetchedDescription: ", error);
    })
  }
  catch(error){
    console.log("ERROR -- FetchedDescription: ", error);    
  }

  return <h2>{description}</h2>
  
}

export default App;
